*, *::before, *::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    font-size: 10px;
}
body{
    font-family: 'Raleway', sans-serif;
}
button{
    padding: 4px 10px;
    background-color: royalblue;
    border: none;
    border-radius: 6px;
    box-shadow: 1px 1px 1px black;
    color: white;
    font-size: 1.6rem;
}
button:disabled{
    background-color: #a8a8a8;
    color : #444444;
}
h2{
    margin: 15px 0;
    font-size: 2.4rem;
    text-align: center;
}
h3{
    display: inline-block;
    font-size: 2rem;
    margin: 10px 20px 30px 0px;
}
main{
    padding: 20px;
}
main, .createContainer, .orderContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
}
/* HEADER */
.header-img{
    position: relative;
    width: 100%;
    height: 20vh;
    background-image: url("./img/pizza-3007395_640.jpg");
    background-position-y: bottom;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 0;
}
.hero-img{
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: -1;
}
.hero-text{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    font-size: 2.2rem;
    color: white;
    font-family: 'Raleway', sans-serif;
    text-shadow: 2px 2px 1px black;
}
/* CREATE */
.pizzaContainer{
    position: relative;
    display: inline-block;
    transition: opacity .3s;
}
.mark {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 60px;
    color: green;
    content: "\2713";
}
.opacity {
    opacity: 0.4;
}
.displayNone{
    display: none;
}
.pizza{
    cursor: pointer;
}
.small{
    width: 7.5rem;
}
.medium{
    width: 9.5rem;
}
.big{
    width: 11.5rem;
}
.ingredientContainer{
    display: grid;
    grid-template-columns: 1fr;
    margin: 20px 0;
}
.ingredient{
    display: flex;
    align-items: center;
    padding: 0 20px
}
.ingredient p{
    font-size: 2rem;
}
.ingredient input{
    transform: scale(1.5)
}
.ingredient p:first-of-type{
    margin-right: 20px
}
.ingredient p:last-of-type{
    margin-left: auto;
}
.ingredientIcon{
    margin: 6px 10px;
    width: 35px;
    height: 35px;
}
/* ORDER */
.orderContainer{
    margin-bottom: 30px;
}
.orderContainer li{
    position: relative;
    padding: 10px;
    margin-right: 20px;
    font-size: 1.8rem;
    text-align: center;
    list-style: none;
}
.inlineDiv{
    display: inline-block;
    padding: 0 30px;
}
.close{
    position: absolute;
    left: 90%;
    bottom: 50%;
    transform: translateY(50%);
    margin: 0px 15px;
}
.paymentContainer{
    margin: 40px;
    font-size: 1.8rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.paymentContainer p:last-of-type{
    margin: 15px 0;
}
/* FOOTER */
footer{
    height: 50px;
    background-color: #222;
    color: white;
    font-size: 1.2rem;
    text-align: center;
}
footer p:first-child{
    padding: 10px 0px 2px;
}
footer a{
    color: white;
    text-decoration: none;
}

@media (min-width: 420px) {
    html{
        font-size: 11px;
    }
}
@media (min-width: 576px) {
    .ingredientContainer{
        grid-template-columns: 1fr 1fr;
    }
    .ingredient p{
        font-size: 1.8rem;
    }
    .inlineDiv{
        padding: 0 50px;
    }
}
@media (min-width: 768px) {
    .header-img{
        background-image: url("./img/pizza-3007395_1280.jpg");
        height: 22vh;
    }
    .small{
        width: 9rem;
    }
    .medium{
        width: 11rem;
    }
    .big{
        width: 13rem;
    }
    .inlineDiv{
        padding: 0 65px;
    }

}
@media (min-width: 992px) {
    .ingredientContainer{
        grid-template-columns: 1fr 1fr 1fr;
    }
    .small{
        width: 10rem;
    }
    .medium{
        width: 12rem;
    }
    .big{
        width: 14rem;
    }
    .inlineDiv{
        padding: 0 90px;
    }
}
@media (min-width: 1200px){
    html{
        font-size: 10px;
    }
    .header-img{
        background-image: url("./img/pizza-3007395_1920.jpg");
        height: 25vh;
    }
    .ingredientContainer{
        grid-template-columns: 1fr 1fr 1fr;
    }
    .createContainer, .orderContainer{
        max-width: 1000px;
    }
}
@media (min-width: 1920px){
    html{
        font-size: 14px;
    }
    .createContainer, .orderContainer{
        max-width: 1200px;
    }
}